const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			num: ''
		};
	},
	onLoad() {
		const loginName = uni.getStorageSync('login_name');
		console.log(loginName)
		const loginPwd = uni.getStorageSync('login_pwd');
	},
	onShow() {
		uni.getStorageInfo({
			success: res =>  {
				console.log(res.keys);
				console.log(res.currentSize,'999');
				// this.num = res.currentSize
				let currentSize = res.currentSize
				console.log(currentSize)
				this.num = currentSize
				console.log(res.limitSize);
			}
		});
	},
	methods: {
		modifyPassword() {
			// 修改密码
			// 进入修改密码页
			uni.navigateTo({
				url: '/pages/forgetPassword'
			});
		},
		delCaching() {
			// 删除系统缓存
			uni.showModal({
				title: '提示',
				content: '确认删除缓存',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						uni.showToast({
							title: '清除中...',
							icon: 'loading',
							duration: 3000
						})
						setTimeout( e =>{
							uni.showToast({
								title: '清除完成',
								icon: 'success',
								duration: 2000
							})
						},3000)
						uni.clearStorageSync();
						setTimeout( e =>{
							this.num = '0'
						},3000)
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		outLogin() {
			// 退出系统--进入登录页
			uni.showModal({
				title: '提示',
				content: '是否退出系统',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						this.runOutLogin()
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		runOutLogin() {
			// 退出系统--进入登录页--执行
			uni.showLoading({
			  title: '退出系统...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/logout`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.state_code == 1) {
						uni.reLaunch({
							url: `/pages/login`
						})
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			})
		}
	}
};
